.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.page {
  align-items: center;
  background-color: var(--background-secondary);
  padding: 10px;
  margin: 30px 70px;
  border-radius: 6px;
  box-shadow: rgb(32, 32, 32) 0px 2px;
}
.page h1 {
  text-shadow: var(--background-secondary) 4px;
  font-size: 1.9rem;
}
.page h2 {
  font-weight: 400;
  font-size: 1rem;
}
